@import url('https://fonts.googleapis.com/css?family=Roboto');
@import url('https://fonts.googleapis.com/css?family=Indie+Flower');

@import url('https://fonts.googleapis.com/css2?family=Baloo+Tamma+2:wght@600&display=swap');

#root {
	width: 100vw;
}

h1, h2, h3, h4, h5, h6 {
	font-weight: lighter;
}
.connexionBox {
	background-color: #eceff1;
	display: flex;
	justify-content: center;
	align-items: center;
	min-height: 100vh;
}
.connexion {
	margin-left: 10vw;
	margin-right: 10vw;
	max-width: 500px;
	font-size: 24px;
}
.connexion > * {
	width: 100%;
}
.connexion > h1{
	font-family: 'Baloo Tamma 2', cursive;
	text-align: center;
	color: #1d1d1d;
}
.connexion > input {
	box-sizing: border-box;
	font-size: 24px;
	padding: 5px;
	border: 5px solid #F44336;
	background-color: #fff;
	border-width: 0 0 5px 0;
	outline: none;
}
button {
	font-size: 20px;
	border: none;
	color: white;
	background-color: #F44336;
	margin-top: 10px;
	padding: 10px;
	cursor: pointer;
	border-radius: 2px;
}
button:disabled {
	background-color: #eceff1;
	cursor:not-allowed;
	color: white;
}
.box {
	display: flex;
	flex-wrap: wrap;
	font-family: 'Roboto', sans-serif;
	color: #212121;
	width: 100vw;
}
.box > * {
	flex: 1 1 100vw;
}
header {
	letter-spacing: 2px;
	text-align: center;
	background-color: #F44336;
	color: white;
	box-shadow: 0px 3px 5px rgba(0,0,0,0.2);
	margin-bottom: 5px;
	padding: 10px 20px;
	font-family: "Baloo Tamma 2";
}
header h1 {
	margin: 0;
	text-shadow: 0px 3px 5px rgba(0,0,0,0.2);
}
header #fireLink {
	color: white;
	text-decoration: none;
}
header #fireLink:hover {
	text-decoration: underline;
}
.rows .row {
	display: inline-block;
}
header #revealHeader {
	padding: 0 5px;
	margin-left: 20px;
}
header #revealHeader:hover {
	border-color: grey;
	cursor: pointer;
}
header #moreInfoHeader {
	border-top: 2px solid white;
	margin: 0 100px;
}
/* ----------------------------
	BvnAdmin
---------------------------- */
div.bvnadmin {
	width: auto;
	margin-left: 5vw;
	margin-right: 5vw;
}
/* ----------------------------
	Conditionnal rendering with filters
---------------------------- */
.filtersRenderCards {
	background-color: #EAEDED;
	box-shadow: 3px 3px 7px #85929E;
	border-radius: 15px;
	padding-bottom: 1vh;
	width: auto;
	margin-left: 2vw;
	margin-right: 5vw;
	/* flex: 0 1 200px; */
	text-align: center;

}
.filterTitle {
	text-align: center;
	font-family: 'Baloo Tamma 2', sans-serif;
	text-align: center;
}
#noMgTitleFilters {
	margin: 5px 20vh;
	border-bottom: 1px solid black;
}
.projectTitle {
	margin-bottom: 5px;
}
.flexRendering {
	padding: 5px 10px;
	display: flex;
	justify-content: space-around;
}
/* ----------------------------
	Cards
---------------------------- */
div.cards {
	margin-top: 10px;
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
}
div.card {
	flex: 0 1 500px;
	padding: 10px 0;
	margin: 20px 15px;
	box-shadow: 0px 3px 5px rgba(0,0,0,0.2);
	border-radius: 2px;
}
.finishedCard {
	flex: 0 1 500px;
	padding: 10px 0;
	margin: 20px 15px;
	box-shadow: 0px 3px 5px rgba(0,0,0,0.2);
	border-radius: 2px;
	background-color: #A9DFBF;
}
#isFinishedLabel {
	font-size: 10px;
}
#isFinished {
	zoom: 0.6;
}
input:checked ~ .isFinishedClass {
	background-color: red!important;
}
#invalid-category {
	font-size: 22px;
	text-transform: uppercase;
	text-align: center;
	font-weight: bold;
	color: red;
	border-top: 2px solid red;
	border-bottom: 2px solid red;
	padding: 5px 0;
}
.infoCardRight {
	font-size: 17px;
	text-align: end;
	margin-top: 0;
}
.recette {
	padding: 0 20px;
}
.recette .description {
	display: flex;
	flex-wrap: wrap;
	width: 100%;
	padding: 5px 20px;
	background-color: #F5F5F5;
	transform: scale(1.12);
	box-shadow: 0px 3px 5px rgba(0,0,0,0.2);
}
.recette .description li {
	margin-left: 20px;
	margin-right: 5px;
}
.recette ul {
	padding: 10px 0px 10px 20px;
}
.recette ul li {
	margin-bottom: 10px;
}
@media all and (max-width:500px) {
	h1 {
		font-size: 1.3em;
	}
	.card {
		margin: 0px;
	}
	header {
		margin: 0;
	}
}
/* ----------------------------
	Footer
---------------------------- */
footer {
	display: flex;
	justify-content: space-around;
	background-color: #eceff1;
	font-size: 12px;
	box-shadow: 0px -3px 5px rgba(0,0,0,0.2);
	width: 100%;
}
/* ----------------------------
	NotFound
---------------------------- */
.notFound {
	display: flex;
	justify-content: center;
	align-items: center;
	min-height: 100vh;
	margin: 0;
	background: #eceff1;
}
/* ----------------------------
	Admin
---------------------------- */
.admin-form > * {
	width: 100%;
	font-size: 18px;
	padding: 5px;
	border: 5px solid #69F0AE;
	border-width: 1px 1px 5px 1px;
	outline: none;
	margin-bottom: 5px;
	font-family: 'Roboto', sans-serif;
}
.card > button {
	width: 100%;
	background: #69F0AE;
}
.ajouter-recette > * 	{
	border: 5px solid #F44336;
	border-width: 1px 1px 5px 1px;
}
.toggleButton {
	margin-left: 30%;
	margin-right: 30%;
	width: auto;
	margin-bottom: 20px;
	border: 1px solid black;
	font-family: 'Roboto', sans-serif;
}
#titleMarkdownHelp {
	padding: 0 20px;
	font-family: "Baloo Tamma 2", sans-serif;
	text-align: center;
	border-bottom: 1px solid black;
	margin: 0 20px;
}
#commandsMarkdown {
	padding: 0 50px;
}
#commandsMarkdown li {
	display: inline;
	padding: 20px;
}
/* ----------------------------
	Login
---------------------------- */
.login {
	padding: 5px;
	text-align: center;
	max-width: 500px;
	margin: auto;
}
.login > * {
	width: 100%;
	
	margin: 0 auto 20px auto;
}